import React, { useContext } from "react";
import { Settings, Storage } from "../app/contexts";
import { getMoneyString } from "./box";

const BoxBody = ({ totalVal }) => {
  const { contentTypes, locale, strings } = useContext(Settings);
  const { readContents, readingTime } = useContext(Storage);
  const readingTimeString = getTimeString(readingTime, locale);
  const contentListString = getContentList(contentTypes, readContents);
  const moneyString = getMoneyString(totalVal, locale);
  const bodyString =
    typeof strings.body === "function"
      ? strings.body(readingTimeString, contentListString, moneyString)
      : strings.body;
  return (
    <p
      className="donation-encourager__body"
      dangerouslySetInnerHTML={{ __html: bodyString }}
    />
  );
};

export default BoxBody;

function getContentList(contentTypes, readContents) {
  return Object.keys(contentTypes)
    .filter(type => Object.prototype.hasOwnProperty.call(readContents, type))
    .filter(type => readContents[type])
    .map((type, i, arr) => {
      const val = readContents[type];
      const isLast = i === arr.length - 1;
      const name = contentTypes[type];
      return `${val} ${val === 1 ? name.singular : name.plural}${
        !isLast ? " und " : ""
      }`;
    })
    .join("");
}

function getTimeString(totalSeconds, locale) {
  const lang = locale.split("-")[0];
  const seconds = totalSeconds % 60;
  const minutes = Math.floor((totalSeconds / 60) % 60);
  const hours = Math.floor(totalSeconds / 3600);
  return `${hoursStr(hours, lang)}
    ${hours && minutes ? " und " : ""}
    ${minutesStr(minutes, !!hours, lang)}
    ${minutes && seconds ? " und " : ""}
    ${secondsStr(seconds, lang)}
  `;

  function hoursStr(hours) {
    return hours ? (hours === 1 ? "1 Stunde" : `${hours} Stunden`) : "";
  }
  function minutesStr(minutes, hasHours) {
    return minutes
      ? minutes === 1
        ? "1 Minute"
        : `${minutes} Minuten`
      : hasHours
      ? ""
      : "0 Minuten";
  }
  function secondsStr(seconds) {
    return seconds ? (seconds === 1 ? "1 Sekunde" : `${seconds} Sekunden`) : "";
  }
}
