import React, { useContext, useState } from "react";
import { Settings, Storage } from "../app/contexts";
import useTwingle from "./twingle-widget";
import useItemSelector from "./item-selector";
import BoxLead from "./box-lead";
import BoxBody from "./box-body";
import "./box.scss";
import "./box-dekoder.scss";

const Box = ({ boxProps, blurRemover, donationFeedback }) => {
  const [isExpanded, setIsExpanded] = useState(boxProps.expanded);
  const [itemSelector, totalVal] = useItemSelector(boxProps);
  const [twingleWidget, onCtaBtnClick] = useTwingle(totalVal, donationFeedback);
  return (
    <aside className="donation-encourager">
      {donationFeedback || twingleWidget || (
        <>
          <BoxLead
            type={isExpanded ? "classic" : "experimental"}
            onClick={() => setIsExpanded(!isExpanded)}
            icon={boxProps.preselectedItems[0].icon}
          />
          <CollapseMe isExpanded={isExpanded}>
            <BoxBody totalVal={totalVal} />
            {itemSelector}
            <BoxCtaButton totalVal={totalVal} onClick={onCtaBtnClick} />
            {blurRemover}
            <BoxFooter />
          </CollapseMe>
        </>
      )}
    </aside>
  );
};

export default Box;

function CollapseMe({ isExpanded, children }) {
  return (
    <div class="donation-encourager__colapse-me" hidden={!isExpanded}>
      {children}
    </div>
  );
}

function BoxCtaButton({ totalVal, onClick }) {
  const { ctaTargetUrl, locale, strings } = useContext(Settings);
  const moneyStr = getMoneyString(totalVal, locale);
  const targetUrl =
    typeof ctaTargetUrl === "function" ? ctaTargetUrl(totalVal) : ctaTargetUrl;
  const buttonString =
    typeof strings.ctaBtn === "function"
      ? strings.ctaBtn(moneyStr)
      : strings.ctaBtn;
  return (
    <p className="donation-encourager__cta">
      <a
        className={`donation-encourager__cta-button${
          !totalVal ? " inactive" : ""
        }`}
        href={onClick ? "#" : targetUrl}
        target="_blank"
        rel="norel noreferrer"
        // tabIndex={isExpanded ? 0 : -1}
        onClick={onClick}
      >
        {buttonString}
      </a>
    </p>
  );
}

function BoxFooter() {
  const { reset } = useContext(Storage);
  const { strings } = useContext(Settings);
  return (
    <p className="donation-encourager__meta">
      <small>
        {strings.footer}{" "}
        <button
          className="donation-encourager__reset-btn"
          onClick={reset}
          // tabIndex={isExpanded ? 0 : -1}
        >
          {strings.resetBtn}
        </button>
      </small>
    </p>
  );
}

export function getMoneyString(amount, locale) {
  return amount.toLocaleString(locale, {
    minimumFractionDigits: amount % 1 ? 2 : 0
  });
}
