import React, { useState } from "react";
import { Portal } from "react-portal";

import { SETTINGS_DEFAULT_DISABLED } from "./settings-default";
import useStorage from "./storage";
import useSiteActions from "./site-actions";
import { Settings, Storage } from "./contexts";

import useContentTracker from "./content-tracker";
import useTimeTracker from "./time-tracker";
import useDonationListener from "./donation-listener";
import useIntrusiveness from "./intrusiveness";
import useBoxPositioner from "./box-positioner";
import useBlur from "./blur";

import Box from "../box/box";

function App({ currentContent }) {
  useContentTracker(currentContent);
  useTimeTracker();
  const donationFeedback = useDonationListener();
  const intrusivenessProps = useIntrusiveness();
  const boxes = useBoxPositioner(intrusivenessProps);
  const blurRemover = useBlur(intrusivenessProps, boxes);
  return (
    <>
      {boxes.map((boxProps, i) => (
        <Portal node={boxProps.wrapperEl} key={i}>
          <Box {...{ boxProps, blurRemover, donationFeedback }} />
        </Portal>
      ))}
    </>
  );
}

function AppController() {
  const [settings, setSettings] = useState(SETTINGS_DEFAULT_DISABLED);
  const storage = useStorage(settings);
  const currentContent = useSiteActions(setSettings, storage);
  return (
    <Settings.Provider value={settings}>
      <Storage.Provider value={storage}>
        <App currentContent={currentContent} />
      </Storage.Provider>
    </Settings.Provider>
  );
}

export default AppController;
